import {Component, Input, OnInit} from '@angular/core';
import {ModalController} from '@ionic/angular';
// tslint:disable-next-line: max-line-length
import {AfricleanAbonnementSubscriptionComponent} from 'src/app/components/africlean-abonnement-subscription/africlean-abonnement-subscription.component';
import {AfricleanAbonnementListComponent} from 'src/app/components/africlean-abonnement-list/africlean-abonnement-list.component';
import {AfricleanModalProvider} from 'src/providers/africlean-modal/africlean-modal.provider';
import {AfricleanSubscribeModel} from 'src/models/abonnement/africlean-subscription.model';
import {AfricleanHttpSubscribeProvider} from 'src/providers/africlean-http/africlean-http-subscribe.provider';
import {AfricleanObservablesProvider} from 'src/providers/africlean-observables/africlean-observables.provider';
import {AfricleanSessionProvider} from 'src/providers/africlean-session/africlean-session.provider';
import {AfricleanUserModel} from "../../../models/africlean-user.model";
import {AfricleanPayementModel} from "../../../models/finances/africlean-payement.model";
import {AfricleanHttpPayementProvider} from "../../../providers/africlean-http/africlean-http-payement.provider";
import {AfricleanConfigProvider} from "../../../providers/africlean-config/africlean-config.provider";
import {StateAdress, StateSubscribe} from "../../../providers/utils/commonEnum";

@Component({
    selector: 'app-abonnement',
    templateUrl: './abonnement.page.html',
    styleUrls: ['./abonnement.page.scss'],
})
export class AbonnementPage implements OnInit {

    /**
     * GÃ¨re l'affichage du text de resiliation
     */
    public displayResiliation = false;

    /**
     * Test si les abonnements en cours de validation ou de resiliation sont prÃ©sent
     */
    public subscribeValidateOrResiliate = false;

    /**
     * Nombre d'abonnements souscrit par le client
     */
    public subscribes: {
        enCours: AfricleanSubscribeModel[],
        validee: AfricleanSubscribeModel[],
        enCoursResiliation: AfricleanSubscribeModel[],
        resilie: AfricleanSubscribeModel[]
    };
    /**
     * Liste des payements
     */
    @Input() public payements: { user: AfricleanUserModel, payement: AfricleanPayementModel } [];
    @Input() public payementsTMP: { user: AfricleanUserModel, payement: AfricleanPayementModel } [];

    @Input() public payementsEmploye: { user: AfricleanUserModel, payement: AfricleanPayementModel } [];
    @Input() public payementsTMPEmploye: { user: AfricleanUserModel, payement: AfricleanPayementModel } [];


    /**
     * numÃ©ro des pages
     */
    public positions = [];
    /**
     * numÃ©ro des pages
     */
    public positionsEmploye = [];

    /**
     * Position de la page courante
     */
    @Input() public currentPage = 1;

    /**
     * Le nombre de clients par pages
     */
    @Input() public nbrPaiementPerPage = 5;
    /**
     * client concernÃ©
     */
    @Input() public userTMP: AfricleanUserModel;

    /**
     * role de l'utilisateur
     */
    @Input() public role: number;

    /**
     * id de l'utilisateur
     */
    @Input() public userId: number;
    /**
     * La limite pour rÃ©cuperer les donnÃ©es
     */
    public limit = 10;

    /**
     * DonnÃ©es Ã  ignorer
     */
    public skip = 0;

    public isWeb: boolean;


    constructor(private modal: AfricleanModalProvider,
                private httpSubscribe: AfricleanHttpSubscribeProvider,
                private observablesProvider: AfricleanObservablesProvider,
                private payementProvider: AfricleanHttpPayementProvider,
                private session: AfricleanSessionProvider,
                private config: AfricleanConfigProvider) {}

    ngOnInit() {
        this.session.getItem('userId').then(
            (userId) => {
                const url = 'abonnement/user-subscribe/' + userId;
                this.loadSubscribes(url);
                this.observablesProvider.subscriptionRequest.subscribe(
                    (isRequest) => {
                        if (isRequest) {
                            this.loadSubscribes(url);
                        }
                    }
                );
            }
        );
        this.getUser();
        this.getPayements('abonnement');
        this.observablesProvider.loadPayement.subscribe(
            (result) => {
                this.payements = [];
                this.payementsTMP = [];
                this.positions = [];
                this.getPayements('abonnement');
            }
        );
    }

    /**
     * Utilisateur temporaire
     */
    public async getUser() {
        this.role = await this.session.getItem('role');
        this.userId = await this.session.getItem('userId');
        this.userTMP = await this.session.getItem('userTMP');
    }

    /**
     * Recharge les abonnements
     * @param url url
     */
    private async loadSubscribes(url: string) {
     //   this.getUser();
        this.httpSubscribe.getSubscribeByUser(url).subscribe(
            (subscribes) => {
                this.subscribes = subscribes;
                // Tester si les abonnements en cours de validation ou en cours de resilitaion sont prÃ©sent
                if (this.subscribes.enCours.length !== 0 || this.subscribes.enCoursResiliation.length !== 0) {
                    this.subscribeValidateOrResiliate = true;
                }
            }
        );
    }

    /**
     * Permet d'afficher le texte de rÃ©siliaion
     */
    public displayText() {
        this.displayResiliation = true;
    }

    /**
     * Annuler la rÃ©siliation
     */
    public annuler() {
        this.displayResiliation = false;
    }

    /**
     * Permet de s'abonner en fournissant l'adresse du lieu
     */
    public async subscription() {
        const props = {
            source: 'consult-abonnement',
            displayForm: true
        };
        this.modal.create(AfricleanAbonnementSubscriptionComponent, 'my-custom-modal-css', props);
    }

    /**
     * La liste des abonnements valider
     */
    public async abonnementsValidateList() {
        if (this.subscribes.validee.length !== 0) {
            this.modal.create(AfricleanAbonnementListComponent, 'my-custom-modal-css');
        }
    }

    /**
     * La liste des abonnements en cours de validation
     */
    public async abonnementsCurrentValidateList() {
        if (this.subscribes.enCours.length !== 0) {
            const props = {
                state: StateAdress.IN_PROGRESS_SUBSCRIB
            };
            this.modal.create(AfricleanAbonnementListComponent, 'my-custom-modal-css', props);
        }
    }

    /**
     * La liste des abonnements en cours de resiliation
     */
    public async abonnementsCurrentResiliationList() {
        if (this.subscribes.enCoursResiliation.length !== 0) {
            const props = {
                state: StateAdress.IN_PROGRESS_TERMINATION
            };
            this.modal.create(AfricleanAbonnementListComponent, 'my-custom-modal-css', props);
        }
    }

    sendPosition(position) {
        this.currentPage = position;
        if (position % 2 === 0 && !this.positions.includes(position)) {
            this.positions.push(position);
            this.skip = this.skip + this.limit;
            this.getPayements('abonnement');
        }
    }

    /**
     * Liste de payement
     */
    public async getPayements(typePayement) {
        this.role = await this.session.getItem('role');
        this.userId = await this.session.getItem('userId');

        let body: any = {};
        let url: string;
        if (this.role === 5) {
            url = 'payement/paiements/client/' + this.userId;
        } else {
            url = 'payement/paiements/';
        }
        body = {
            limit: this.limit,
            skip: this.skip
        };

        this.payementProvider.postPaiment(url, body).subscribe(
            (payementList) => {
                if (!this.payements && !this.payementsTMP) {
                    this.payements = [];
                    this.payementsTMP = [];
                    this.positions = [];
                }
                this.payements = this.payements.concat(payementList.slice());
                this.payementsTMP = this.payementsTMP.concat(payementList.slice());
            }
        );
    }
}
